.page{position:relative;z-index:2;background:#fff;min-height:100vh;padding-bottom:4rem}.container{max-width:1400px;margin:0 auto;padding:8rem 3rem 0}.title{font-size:2.5rem;font-weight:300;letter-spacing:-0.02em;color:#000;margin:0 0 4rem;line-height:1.1}.exhibitionGrid{display:grid;gap:4rem}.exhibitionItem{display:grid;grid-template-columns:1fr 1fr;gap:3rem;align-items:start}.exhibitionItem:nth-child(even) .imageContainer{order:2}.exhibitionItem:nth-child(even) .exhibitionInfo{order:1;text-align:right}.imageContainer{position:relative;overflow:hidden}.image{width:100%;height:auto;object-fit:cover;transition:transform .3s ease}.image:hover{transform:scale(1.02)}.exhibitionInfo{padding:1rem 0}.status{display:inline-block;font-size:.8rem;font-weight:600;text-transform:uppercase;letter-spacing:.1em;color:#666;margin-bottom:1rem}.status:before{content:"";display:inline-block;width:8px;height:8px;border-radius:50%;margin-right:.5rem;background:#ccc}.exhibitionItem:first-child .status{color:var(--color-green)}.exhibitionItem:first-child .status:before{background:var(--color-green)}.exhibitionTitle{font-size:1.5rem;font-weight:400;color:#000;margin:0 0 .5rem;line-height:1.3}.exhibitionDate{font-size:.9rem;color:#666;margin:0 0 1rem;font-weight:500}.exhibitionDescription{font-size:1rem;line-height:1.6;color:#333;margin:0}@media(max-width: 1024px){.container{padding:7rem 2rem 0}.exhibitionItem{gap:2rem}}@media(max-width: 768px){.container{padding:6rem 1.5rem 0}.title{font-size:2rem;margin-bottom:3rem}.exhibitionGrid{gap:3rem}.exhibitionItem{grid-template-columns:1fr;gap:1.5rem}.exhibitionItem:nth-child(even) .imageContainer,.exhibitionItem:nth-child(even) .exhibitionInfo{order:initial}.exhibitionItem:nth-child(even) .exhibitionInfo{text-align:left}.exhibitionTitle{font-size:1.3rem}}@media(max-width: 480px){.container{padding:5rem 1rem 0}.title{font-size:1.75rem}.exhibitionTitle{font-size:1.2rem}.exhibitionDescription{font-size:.95rem}}